/* Estilo general para el footer */
.footer {
    width: 100%;
    background-color: #0A1F3A;
    color: #E0FFFF;
    padding: 20px 40px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px -5px 15px rgba(0, 0, 0, 0.2);
    border-top: 2px solid rgba(224, 255, 255, 0.5);
    margin-top: auto; /* Empuja el footer siempre abajo */
}

/* Estilos para la navegación del footer */
.footer-nav {
    display: flex;
    gap: 30px;
    justify-content: center;
    align-items: center;
    margin: 10px 0;
    flex-wrap: wrap; /* Permite que los elementos se acomoden en pantallas pequeñas */
}

/* Información adicional en el footer */
.footer-info {
    display: flex;
    width: 100%;
    max-width: 1200px;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid rgba(224, 255, 255, 0.3);
    flex-wrap: wrap; /* Ajuste en dispositivos pequeños */
}

/* Estilos para enlaces dentro del footer */
.footer-nav a,
.footer-info a {
    color: #E0FFFF;
    text-decoration: none;
    transition: color 0.3s, transform 0.3s;
    font-weight: bold;
}

.footer-nav a {
    font-size: 1.1rem;
}

.footer-nav a:hover,
.footer-info a:hover {
    color: #339EB2;
    transform: scale(1.1);
}

/* Estilo para los derechos reservados */
.footer-info div {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.9rem;
    opacity: 0.8;
    flex: 1; /* Para que se distribuyan equitativamente */
    text-align: center;
}

/* Alineación en desktop */
.footer-info div:first-child {
    justify-content: flex-start;
    text-align: left;
}

.footer-info div:last-child {
    justify-content: flex-end;
    text-align: right;
}

/* Responsividad para pantallas pequeñas */
@media (max-width: 768px) {
    .footer-nav {
        flex-direction: column;
        text-align: center;
        line-height: 2;
    }
    
    .footer-info {
        flex-direction: column;
        align-items: center;
        gap: 5px;
        padding-top: 15px;
        text-align: center;
    }

    .footer-info div {
        justify-content: center !important;
        text-align: center !important;
    }
}
