.que-predecimos-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: black;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding-top: 80px; /* Reserva el espacio del header */
}

.content-header {
  text-align: center;
  margin-bottom: 20px;
}

.content-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: #177f5a;
  font-family: 'Jersey M54', sans-serif;
}

.content-header .highlight {
  color: #10593f;
}

.content-body {
  flex: 1;
  max-width: 900px;
  margin: 20px auto;
  padding: 20px;
  background: #e4e9f2;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.4);
  overflow-y: auto; /* Permite scroll si hay demasiado contenido */
  max-height: calc(100vh - 130px); /* Considera el header y un margen adicional */
}

.content-body p,
.content-body ul {
  font-size: 1.1rem;
  line-height: 1.8;
  margin-bottom: 15px;
}

.content-body ul {
  padding-left: 20px;
}

.content-body ul li {
  margin-bottom: 10px;
  color: black;
}

.content-body ul li::marker {
  color: #177f5a;
}

/* Ajustes responsivos */
@media (max-width: 768px) {
  .content-header h2 {
    font-size: 1.8rem;
  }
  .content-body {
    padding: 15px;
    margin: 10px auto; /* Centra el contenido en dispositivos más pequeños */
  }
}

@media (max-width: 480px) {
  .content-header h2 {
    font-size: 1.5rem;
  }
  .content-body {
    padding: 10px;
    margin: 5px auto; /* Reduce el margen en pantallas más pequeñas */
  }
}
