
/* Estilos globales */
@font-face {
  font-family: 'Roboto';
  font-weight: 400;
  src: url('../../public/fonts/roboto-regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Roboto-bold';
  font-weight: 700;
  src: url('../../public/fonts/roboto-bold.ttf') format('truetype');
}

@font-face {
  font-family: 'Merriweather';
  font-weight: 700;
  src: url('../../public/fonts/merriweather-bold.ttf') format('truetype');
}

@font-face {
  font-family: 'Jersey M54';
  src: url('../../public/fonts/jersey-m54.ttf') format('truetype');
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body, html {
  font-family: 'Roboto-bold', sans-serif;
  background-color: #f4f7fc;
  color: #333;
  height: 100%;
}

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.compare-page {
  text-align: center;
  padding: 20px;
  flex: 1;
}

.compare-container {
  max-width: 1200px;
  margin: auto;
  position: relative;
 
}

.players-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 30px;
}

.player-box {
  width: 300px;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  text-align: center;
  transition: transform 0.2s ease-in-out, box-shadow 0.3s ease;
  border: 2px solid #2E4A67;
}

.player-box:hover {
  transform: scale(1.03);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
}

.player-box.winner {
  border: 2px solid #28a745;
  background-color: #87f587;
}

.player-box.loser {
  border: 2px solid #e63946;
  background-color: #ff9191;
}

.player-box.draw {
  border: 2px solid #f0ad4e;
  background-color: #f8f9fa;
}

.player-info {
  background: linear-gradient(135deg, #0A1F3A, #2E4A67);
  border-radius: 15px;
  padding: 15px;
  border-bottom: 4px solid #177f5a;
  border-top: 4px solid #177f5a;
}

.player-info h3 {
  color: #e4e9f2;
  margin-bottom: 10px;
}

.player-image {
  width: 120px; /* Aumentado el tamaño de la imagen */
  height: 120px; /* Aumentado el tamaño de la imagen */
  border-radius: 10%;
  border: 3px solid #E0F7FA;
  margin: 15px 0;
  object-fit: cover;
}

.stats {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
  background: linear-gradient(145deg, #f7f9fc, #e4e9f2);
  padding: 15px;
  border-radius: 15px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  width: 100%; /* Ocupa todo el ancho disponible */
  overflow-x: auto; /* Para que quepan todas las barras si la pantalla es pequeña */
}

.stat-bar-container {
  display: flex;
  flex-direction: column; /* Columnas para la barra y el texto */
  align-items: center; /* Centra horizontalmente */
  width: 50px; /* Ancho fijo para las barras */
  height: 250px; /* Altura fija para las barras */
  /* Eliminamos position: relative; */
  justify-content: flex-end; /* Alinea la barra al final */

}

.probability-bar {
  width: 100%; /* Ocupa todo el ancho del contenedor */
  /* Eliminamos position: absolute; */
  bottom: 0; /* La barra empieza desde abajo */
  /* Usamos flex-grow para que la barra se expanda */
  flex-grow: 1;
  max-height: 100%; /* Esto ahora funciona correctamente */
  box-shadow: inset 0 2px 5px rgba(0, 0, 0, 0.2), 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: height 0.6s ease-in-out;
}

.stat-bar-container span {
  margin-top: 5px; /* Espacio entre la barra y el texto */
  text-align: center;
}

:root {
  --rojo: linear-gradient(180deg, #ff7979, #e63946);
  --verde: linear-gradient(180deg, #5cd65c, #28a745);
  --azul: linear-gradient(180deg, #68c5db, #339EB2);
  --naranja: linear-gradient(180deg, #ffcc66, #ff8c00);
}

.stat-bar-container span {
  margin-top: 8px;
  font-size: 0.95em;
  text-align: center;
  font-weight: bold;
  color: #333;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

@keyframes fillBar {
  from { height: 0; }
  to { height: var(--bar-height); }
}

.probability-bar { animation: fillBar 1s ease-in-out; }

.ad-space {
  background-color: #efefef;
  height: 50px;
  margin-bottom: 20px;
  margin-right: 5px;
  margin-left: 5px;
}
.ad-space-start{
  background-color: #efefef;
  margin-top: 90px;
}


.input-container {
  position: relative;
  margin-bottom: 15px;
}

.clear-btn {
  position: absolute;
  right: 5px;
  top: 5px;
  border: none;
  background: transparent;
  cursor: pointer;
}

.suggestions {
  list-style: none;
  padding: 0;
  border: 1px solid #ccc;
  background: white;
  position: absolute;
  width: 100%;
  z-index: 10;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.suggestions li {
  padding: 5px;
  cursor: pointer;
}

.suggestions li:hover {
  background: #f0f0f0;
}

.compare-button {
  margin-top: 20px;
  background-color: #28a745;
  color: white;
  padding: 12px 25px;
  border: none;
  cursor: pointer;
  border-radius: 8px;
  font-size: 1rem;
  transition: background 0.3s ease;
  width: 30%;
  align-items: center;
  justify-content: center;
}

.compare-button:hover {
  background-color: #218838;
}

/* Rúbrica */
.rubrica-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-bottom: 0px;
  margin-top: 10px;
}

.rubrica-btn {
  background-color: #28a745;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  border-radius: 8px;
  font-size: 1rem;
  transition: background 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.rubrica-btn:hover {
  background-color: #218838;
}

.rubrica-content {
  margin-top: 10px;
  background-color: #fff;
  color: #fff;
  padding: 15px;
  border-radius: 8px;
}

.rubrica-content table {
  width: 100%;
  margin-top: 10px;
  border-radius: 8px;
  color: #E0F7FA;
  
}
.rubrica-content table h2{
  width: 100%;
  margin-top: 10px;
  border-radius: 8px;
  
}

.rubrica-content th, .rubrica-content td {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 10%;
  
}

.header-rojo { background: var(--rojo); color: white; }
.header-naranja { background: var(--naranja); color: white; }
.header-verde { background: var(--verde); color: white; }
.header-azul { background: var(--azul); color: white; }

.arrow-icon {
  transition: transform 0.3s ease;
}

.arrow-icon.open {
  transform: rotate(180deg);
}

@media (max-width: 750px) {
  .player-box { width: 250px; }
  .player-image { width: 90px; height: 90px; } /* Aumentado el tamaño de la imagen */
  .rubrica-container { margin: 10px 0; }
  .player-box {
    width: 300px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
    text-align: center;
    transition: transform 0.2s ease-in-out, box-shadow 0.3s ease;
    border: 2px solid #2E4A67;
  }
}

/* Mejoras en las barras de estadísticas */
.stat-bar-container {
  height: 200px; /* Altura reducida para mejor proporción */
}

/* Etiquetas de valor numérico */
.bar-value {
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 4px 8px;
  border-radius: 5px;
  font-size: 0.8em;
  font-weight: bold;
  opacity: 0;
  transition: opacity 0.3s ease;
  white-space: nowrap;
}

.stat-bar-container:hover .bar-value {
  opacity: 1;
}

/* Botón de comparar mejorado */
.compare-button {
  background: linear-gradient(135deg, #2E4A67, #0A1F3A);
  padding: 15px 40px;
  font-size: 1.1em;
  font-weight: bold;
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  margin: 30px auto;
  display: flex;
  align-items: center;
  gap: 10px;
  border: 2px solid #E0F7FA;
  top:0;
  }
  
  .compare-button:disabled {
    background: #cccccc;
    cursor: not-allowed;
  }
  
  .compare-button:not(:disabled):hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 25px rgba(42, 139, 242, 0.3);
  }
  
  /* Rúbrica mejorada */
  .rubrica-btn {
    background: linear-gradient(135deg, #2E4A67, #0A1F3A);
    padding: 12px 25px;
    font-weight: bold;
    transition: all 0.3s ease;
  }
  
  .rubrica-btn:hover {
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  }
  
  /* Animación para el ganador */
  @keyframes pulse-win {
    0% { transform: scale(1); }
    50% { transform: scale(1.02); }
    100% { transform: scale(1); }
  }
  
  .player-box.winner {
    animation: pulse-win 1.5s ease-in-out infinite;
    box-shadow: 0 0 25px rgba(40, 167, 69, 0.3);
  }
  
  /* Mejoras responsive */
  @media (max-width: 768px) {
    
    
    .rubrica-container {
      order: 3;
      width: 100%;
      margin-top: 20px;
    }
    
    .compare-button {
      width: 90%;
      justify-content: center;
    }
  }
  
  /* Estilos mejorados para las barras y responsive */
  .player-box {
    position: relative;
    overflow: hidden;
  }
  
  .stats {
    min-height: 280px;
    padding: 20px 15px;
    margin-top: 15px;
  }
  
  .stat-bar-container {
    height: 220px;
    justify-content: flex-start;
    position: relative;
    margin: 0 8px;
  }
  
  .probability-bar {
    width: 32px;
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    transition: height 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    max-height: calc(100% - 40px); /* Asegura que no salga del contenedor */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  .stat-bar-container span {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 0.85rem;
    font-weight: 600;
    color: #2E4A67;
    white-space: nowrap;
    
    padding: 2px 8px;
  }
  
  /* Ajustes responsive */
  @media (max-width: 768px) {
    
    
    .player-box {
      width: 100%;
      max-width: 400px;
    }
    
    .stats {
      min-height: auto;
      padding: 15px 10px;
    }
    
    .stat-bar-container {
      height: 180px;
      margin: 0 5px;
    }
    
    .probability-bar {
      width: 28px;
    }
    
    .stat-bar-container span {
      font-size: 0.8rem;
      padding: 2px 6px;
    }
  }
  
 
  
  /* Mejoras en las transiciones */
  .player-box {
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }
  
  .probability-bar {
    transition: height 0.6s cubic-bezier(0.68, -0.55, 0.27, 1.55), 
                box-shadow 0.3s ease;
  }
  
  /* Ajuste posición vertical del texto */
  .stat-bar-container {
    display: flex;
    flex-direction: column-reverse; /* Invierte el orden para texto arriba */
    align-items: center;
    height: 240px;
  }
  
  /* Asegurar que las barras no se salgan */
  .player-info {
    position: relative;
    overflow: visible;
  }
  
  .stats {
    overflow: visible;
    position: relative;
    padding-bottom: 40px; /* Espacio para las etiquetas */
  }
  
  
  
  /* Ajustes para los inputs de búsqueda */
  .input-container input {
    color: #000 !important; /* Texto negro */
    caret-color: #000; /* Cursor negro */
    padding-right: 35px;
    border: 2px solid #2E4A67;
    border-radius: 8px;
    width: 100%;
    height: 40px;
    font-size: 1rem;
  }
  
  .input-container input::placeholder {
    color: #666;
  }
  
  /* Sugerencias sobre todo el contenido */
  .suggestions {
    z-index: 1000; /* Mayor que todo */
    top: 100%;
    margin-top: 5px;
    border-radius: 8px;
    background: #fff;
    max-height: 200px;
    overflow-y: auto;
  }
  
  /* Contenedor principal ajustado */
  .players-container {
    position: relative;
    min-height: 500px; /* Espacio para la rúbrica */
    padding-top: 60px; /* Espacio para sugerencias */
  }
  
  /* Ajuste de z-index en cajas de jugador */
  .player-box {
    position: relative;
    z-index: 1;
  }
  
  /* Asegurar que las sugerencias se ven completas */
  .player-box {
    overflow: visible;
  }
  
  .input-container {
    position: static;
  }
  
  /* Responsive para móviles */
  @media (max-width: 768px) {
    .rubrica-container {
      transform: none;
      margin: 20px 0;
      order: 3;
      width: 100%;
    }
    
    .players-container {
      min-height: auto;
      padding-top: 0;
    }
    
    .player-box {
      width: 100%;
      margin-bottom: 20px;
    }
    
    .suggestions {
      width: calc(100% - 30px);
      left: 15px;
    }
  }
  
  /* Mejora visual de la rúbrica */
  .rubrica-btn {
    background: linear-gradient(135deg, #2E4A67, #0A1F3A);
    color: white;
    padding: 12px 25px;
    border-radius: 8px;
    font-weight: bold;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    border: 2px solid #E0F7FA;
  }
  
  .rubrica-content {
    position: relative;
    margin-top: 15px;
  }
  .compare-page {
    padding-top: 15px; /* Ajusta según la altura real del header */
    margin-top: 80px;
  }
  .compare-page h2 {
    font-family: 'Archivo Black', sans-serif;
    font-size: 1.8rem; /* Tamaño un poco mayor para destacar */
    color: #ffffff; /* Texto blanco */
    text-align: center;
    margin: 0;
    padding: 15px 40px; /* Más padding para hacerlo más prominente */
    text-transform: uppercase;
    border-radius: 12px; /* Bordes más redondeados */
    /* Borde blanco con sombra */
    border: 2px solid #ffffff;
    background: linear-gradient(135deg, #004d60, #007a2e); /* Gradiente sutil */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7); /* Sombra para que el texto resalte */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Sombra del contenedor */
    transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
    cursor: pointer; /* Indicador de clic */
    margin-bottom: 20px;
}

.compare-page h2:hover {
    text-shadow: 4px 4px 8px rgba(0, 0, 0, 0.7);
}

