/* ------------------------------
   Contenedor general de la página
------------------------------ */
.noticia-detalle-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background: #f9f9f9;
    color: #333;
    font-family: 'Roboto', sans-serif;
    padding-top: 90px;
  }
  
  /* ------------------------------
     Estructura de tres columnas con espacio y centrado
  ------------------------------ */
  .noticia-detalle-layout {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    margin: 0 auto;
    padding: 20px;
    box-sizing: border-box;
    
  }
  
  /* ------------------------------
     Columnas laterales para anuncios
  ------------------------------ */
  .ad-lateral{
    width: 160px;
    height: 600px; /* Altura estándar para anuncios laterales */
    display: none; /* Oculto por defecto, visible en pantallas grandes */
    position: sticky;
    top: 50%; /* Se posiciona en la mitad de la pantalla */
    transform: translateY(-50%); /* Centrado vertical */
    background: #f1f1f1;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    padding: 0;
    overflow: hidden; /* Evita desbordes */
    box-sizing: border-box;
  }
  
  /* Contenedor interno del anuncio */
  .ad-container {
    width: 100%;
    height: 100%;
    background-color: #cccccc; /* Simulación de espacio para anuncio */
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .ad-container iframe {
    width: 100%;
    height: 100%;
    border: none;
    display: block;
  }
  
  /* ------------------------------
     Columna central (contenido de la noticia)
  ------------------------------ */
  .noticia-div {
    flex: 1;
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 6px 14px rgba(0, 0, 0, 0.1);
    padding: 30px;
    max-width: 900px;
    width: 100%;
    animation: fadeIn 0.8s ease-in-out;
  }
  
  /* Título de la noticia */
  .noticia-detalle-container h1 {
    font-family: 'Archivo Black', sans-serif;
    font-size: 2.3rem;
    text-align: center;
    margin-bottom: 15px;
    color: #222;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
  }
  
  /* Imagen principal */
  .image-container {
    text-align: center;
    margin: 20px 0;
  }
  
  .news-image-detail {
    width: 100%;
    max-height: 450px;
    object-fit: cover;
    border-radius: 8px;
    transition: transform 0.3s ease-in-out;
  }
  
  .news-image-detail:hover {
    transform: scale(1.03);
  }
  .news-image-container {
    position: relative;
    width: 100%;
    height: 200px; /* Altura fija del contenedor */
    overflow: hidden; /* Para recortar la imagen sobrante */
  }
  
  .news-image {
    width: 100%;
    height: 100%;
    object-fit: cover;       /* Ajusta la imagen sin deformarla */
    object-position: center; /* Centra la imagen */
    display: block;
    transition: filter 0.3s ease;
  }
  /* Fecha de publicación */
  .noticia-fecha {
    text-align: right;
    font-size: 0.95rem;
    color: #666;
    margin-bottom: 10px;
  }
  
  /* Texto de la noticia */
  .noticia-texto {
    font-size: 1.1rem;
    line-height: 1.7;
    color: #444;
    text-align: justify;
  }
  
  /* ------------------------------
     Cargando noticia
  ------------------------------ */
  .loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: #f9f9f9;
  }
  
  .loading-text {
    font-size: 1.2rem;
    color: #666;
  }
  
  .noticia-detalle p
  {
    margin-top: 10px;
    margin-bottom: 10px;
  }
  /* ------------------------------
     Responsividad
  ------------------------------ */
  
  /* Mostrar anuncios laterales solo en pantallas grandes */
  @media (min-width: 1024px) {
    .ad-lateral {
      display: block;
    }
  }
  
  /* Ajustes para tabletas */
  @media (max-width: 768px) {
    .noticia-detalle-container h1 {
      font-size: 2rem;
    }
  
    .noticia-div {
      padding: 20px;
    }
  
    .noticia-texto {
      font-size: 1rem;
    }
  
    /* Ocultar anuncios en tabletas */
    .ad-lateral {
      display: none;
    }
  }
  
  /* Ajustes para móviles */
  @media (max-width: 480px) {
    .noticia-detalle-container h1 {
      font-size: 1.7rem;
    }
  
    .noticia-texto {
      font-size: 0.95rem;
    }
  }
  
  /* ------------------------------
     Animación de entrada
  ------------------------------ */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-8px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  