@font-face {
  font-family: 'Roboto';
  font-weight: 400;
  src: url('../../public/fonts/roboto-regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Roboto-bold';
  font-weight: 700;
  src: url('../../public/fonts/roboto-bold.ttf') format('truetype');
}

@font-face {
  font-family: 'Merriweather';
  font-weight: 700;
  src: url('../../public/fonts/merriweather-bold.ttf') format('truetype');
}

@font-face {
  font-family: 'Jersey M54';
  src: url('../../public/fonts/jersey-m54.ttf') format('truetype');
}

* {
  box-sizing: border-box;
}

body, html {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: 'Roboto-bold', sans-serif;
  background: linear-gradient(90deg, #000, #80deea, #000);
  color: #333;
  
}

.idealxi-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 20px;
  background-color: #000;
  min-height:80vh ;
  margin-top: 90px;
  
}

.idealxi-title {
  margin-top: 60px;
  min-width: 600px;
  font-size: 3em;
  margin-bottom: 20px;
  color: #FFD700;
  text-align: center;
  font-family: 'Jersey M54', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7);
  background: linear-gradient(90deg, #FFD700, #FFA500);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  border: 2px solid #FFD700;
  border-radius: 8px;
  display: inline-block;
}

.field {
  display: grid;
  gap: 20px;
  width: 100%;
  max-width: 600px;
  padding: 20px;
  position: relative;
  border-top: 2px solid #FFD700;
  border-bottom: 2px solid #FFD700;
  background: linear-gradient(135deg, #0A1F3A, #2E4A67);
  border-radius: 15px;
}

/* Posicionamiento de las filas para simular la formación */
.row {
  display: flex;
  width: 100%;
}

.portero {
  justify-content: center;
}

.defensores {
  justify-content: space-around;
}

.mediocentros {
  justify-content: center;
  gap: 0px;
  display: flex;
}

.delanteros {
  justify-content: space-between;
}

.player-card {
  text-align: center;
  color: #fff;
  font-weight: bold;
}

.player-card p {
  margin: 5px 0;
  font-size: 0.9em;
}

.player-card img {
  width: 60px;
  height: 60px;
  border-radius: 10%;
  border: 2px solid #FFD700;
  background: radial-gradient(circle, rgba(0, 206, 209, 0.8), #003366, rgba(0, 0, 0, 0.9));
}

.mediocentros .player-card {
  flex: 1 1 0;
}

.mediocentros .player-card:nth-child(2) {
  margin-left: auto;
  margin-right: auto;
}

.idealxi-container.blureado {
  filter: blur(7.5px);
  pointer-events: none;
  user-select: none;
}

.overlay {
  position: absolute; /* Posicionado dentro del contenedor */
  top: 50%; /* Centrado verticalmente */
  left: 50%; /* Centrado horizontalmente */
  transform: translate(-50%, -50%); /* Ajusta el centrado absoluto */
  width: auto; /* Ajusta el ancho automáticamente */
  max-width: 90%; /* Limita el ancho para pantallas pequeñas */
  padding: 20px; /* Espaciado interno */
  font-family: 'Archivo Black', sans-serif;
  color: white;
  text-align: center;
  font-size: 2rem; /* Tamaño del texto */
  z-index: 1000; /* Asegura que esté encima del contenido */
  background: rgba(0, 0, 0, 0.7); /* Fondo semitransparente oscuro */
  border-radius: 10px; /* Bordes redondeados */
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.5); /* Añade sombra para destacar */
  pointer-events: none; /* No interactuable */
  user-select: none; /* No seleccionable */
  margin-bottom: 50px;
}



@media (max-width: 768px) {
  .overlay {
    font-size: 1.5rem; /* Ajusta el tamaño del texto para pantallas medianas */
    padding: 15px; /* Reduce el espaciado interno */
  }
}

@media (max-width: 480px) {
  .overlay {
    font-size: 1.2rem; /* Ajusta el tamaño para pantallas pequeñas */
    padding: 10px; /* Reduce aún más el espaciado */
  }
}

