.quienes-somos-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding-top: 80px; /* Reserva para el Header */
}

.content-header {
  text-align: center;
  margin-bottom: 20px;
}

.content-header h2 {
  font-size: 2.5rem;
  font-weight: 700;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  
  font-family: 'Jersey M54', sans-serif;
}


.content-body {
  flex: 1;
  max-width: 900px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.4); /* Sombra ligera */
  overflow-y: auto; /* Permite scroll si el contenido es largo */
  max-height: calc(100vh - 120px); /* Ajusta para que no sobrepase la pantalla, dejando espacio al footer */
}

.content-body p,
.content-body ul {
  font-size: 1.1rem;
  line-height: 1.8;
  margin-bottom: 15px;
}

.content-body ul {
  padding-left: 20px;
}





/* Ajustes responsivos */
@media (max-width: 768px) {
  .content-header h2 {
    font-size: 1.8rem;
  }
  .content-body {
    padding: 15px;
  }
  .content-body p,
  .content-body ul {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .content-header h2 {
    font-size: 1.5rem;
  }
  .content-body {
    padding: 10px;
  }
  .content-body p,
  .content-body ul {
    font-size: 0.95rem;
  }
}
