.contacto-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background: linear-gradient(to bottom right, #004d60, #001f29);
    color: #f8f8ff;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-top: 80px;
  }
  
  .contacto-container {
    width: 100%;
    height: 100%;
    max-width: 600px;
    margin: auto;
    background-color: #f8f8ff;
    padding: 25px;
    border-radius: 10px;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.5);
    text-align: center;
    
  }
  
  .contacto-container h2 {
    margin-top: 10px;
    font-size: 2.5rem;
    color: #177f5a;
    margin-bottom: 10px;
  }
  
  .contacto-container .intro-text {
    font-size: 1.2rem;
    color: black;
    margin-bottom: 20px;
  }
  
  .success-message {
    font-size: 1rem;
    color: #177f5a;
    margin-bottom: 20px;
  }
  
  form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  label {
    text-align: left;
    font-weight: bold;
    color: black;
  }
  
  input, textarea {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    background: #dadae1;
    color: black;
    outline: none;
    transition: background 0.3s ease;
  }
  
  input:focus, textarea:focus {
    background: rgba(255, 255, 255, 0.2);
  }
  
  textarea {
    height: 150px;
    resize: none;
  }
  
  .button {
    padding: 12px 20px;
    border: none;
    border-radius: 5px;
    background-color: #177f5a;
    color: #f8f8ff;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  .button:hover {
    background-color: #177f5a;
    transform: scale(1.05);
  }
  
  @media (max-width: 768px) {
    .contacto-container {
      padding: 20px;
    }
  
    .contacto-container h2 {
      font-size: 2rem;
    }
  
    .contacto-container .intro-text {
      font-size: 1rem;
    }
  }
  