/* =============================
   FUENTES PERSONALIZADAS
   ============================= */
   @font-face {
    font-family: 'Roboto';
    font-weight: 400;
    src: url('/public/fonts/roboto-regular.ttf') format('truetype');
  }
  @font-face {
    font-family: 'Roboto';
    font-weight: 700;
    src: url('/public/fonts/roboto-bold.ttf') format('truetype');
  }
  @font-face {
    font-family: 'Merriweather';
    font-weight: 700;
    src: url('/public/fonts/merriweather-bold.ttf') format('truetype');
  }
  @font-face {
    font-family: 'Jersey M54';
    src: url('/public/fonts/jersey-m54.ttf') format('truetype');
  }
  @font-face {
    font-family: 'Archivo Black';
    src: url('/public/fonts/ArchivoBlack-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
  }
  
  /* =============================
     RESETEO DE ESTILOS GENERALES
     ============================= */
  * {
    box-sizing: border-box;
  }
  
  body, html {
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    width: 100%;
    /* Gradiente de fondo */
    background: linear-gradient(to bottom right, #001f29, #004d60, #599a71) !important;
    min-height: 100vh;
    font-family: 'Roboto', sans-serif;
  }
  
  .container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background: transparent;
  }
  
  /* =============================
     TÍTULO PRINCIPAL: Guía Laliga Fantasy
     ============================= */
  
  /* Contenedor del título principal */
  .main-featured-box {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* Fondo minimalista */
    background: #0a1f3a;
    box-shadow: none;
    padding: 25px 0;
    border-bottom: 1px solid #007a2e;
  }
  
  /* Título principal interactivo */
  .featured-title {
    font-family: 'Archivo Black', sans-serif;
    font-size: 1.8rem; /* Tamaño un poco mayor para destacar */
    color: #ffffff; /* Texto blanco */
    text-align: center;
    margin: 0;
    padding: 15px 40px; /* Más padding para hacerlo más prominente */
    text-transform: uppercase;
    border-radius: 12px; /* Bordes más redondeados */
    /* Borde blanco con sombra */
    border: 2px solid #ffffff;
    background: linear-gradient(135deg, #004d60, #007a2e); /* Gradiente sutil */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7); /* Sombra para que el texto resalte */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Sombra del contenedor */
    transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
    cursor: pointer; /* Indicador de clic */
  }

  /* Efecto al pasar el cursor */
  .featured-title:hover {
    background: linear-gradient(135deg, #007a2e, #004d60); /* Gradiente invertido */
    transform: scale(1.05); /* Aumenta ligeramente el tamaño */
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.4), 0 0 10px #00ff6b; /* Sombra más intensa */
  }

  /* Efecto al hacer clic */
  .featured-title:active {
    transform: scale(0.97); /* Se hunde ligeramente */
    box-shadow: 0 3px 7px rgba(0, 0, 0, 0.2); /* Menor sombra */
    background: linear-gradient(135deg, #003f4d, #005a1e); /* Gradiente más oscuro */
  }

  /* =============================
     CONTENEDOR DE BOTONES (DELANTEROS, MEDIOCENTROS, ETC.)
     ============================= */
  .clickable-boxes {
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow-x: auto;
    padding: 10px 0;
    background-color: #0A1F3A;
    gap: 0;
    border-bottom: 1px solid #00a73d;
    border-top: 2px solid #00a73d;

  }
  
  /* Clase visible para mostrar las cajas */
  .clickable-boxes.visible {
    opacity: 1;
    visibility: visible;
    height: auto;
  }
  
  
  .clickable-square {
    width: 100%;
    height: 100%;
    color: #e0f7fa;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Archivo Black', sans-serif;
    font-size: clamp(0.5rem, 1.2vw, 0.8rem);
    white-space: nowrap;
    text-transform: uppercase;
    border-right: 2px solid #177f5a;
    box-shadow: none;
    cursor: pointer;
    flex: 1 1 auto;
    padding: 6px;
    transition: background-color 0.3s ease, transform 0.2s ease, box-shadow 0.3s ease;
  }
  
  .clickable-square img {
    height: 70px;
    width: 70px;
    border: 1px solid #003f4d;
    padding: 10px;
    border-radius: 20%;
    margin-bottom: 7.5px;
  }
  
  .clickable-square:last-child {
    border-right: none;
  }
  
  .clickable-square img:hover {
    background-color: #177f5a;
    transform: scale(1.02);
  }
  
  
  /* =============================
     SUBTÍTULO: PREDICCIONES LALIGA FANTASY JORNADA X
     ============================= */
  
  /* Contenedor del subtítulo */
  .centralcontent-title {
    display: flex;
    background-color: transparent;
    margin: 25px;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
    animation: fadeIn 2s ease-in-out;
  }
  
  /* Subtítulo con gradiente y borde */
  .predictions-title {
    font-family: 'Archivo Black', sans-serif;
    color: #ffffff;
    font-size: 1.8rem;
    margin: 0;
    padding: 10px 25px;
    text-transform: uppercase;
    border-radius: 8px;
    border: 2px solid #ffffff;
    text-shadow: 2px 2px 5px rgba(0,0,0,0.7);
    background: linear-gradient(135deg, #004d60, #007a2e); /* Gradiente sutil */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7); /* Sombra para que el texto resalte */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Sombra del contenedor */
    animation: pulse 2s infinite ease-in-out, colorGlow 4s infinite ease-in-out;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .predictions-title:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px #00a73d, 0 10px 20px rgba(0, 0, 0, 0.4);
    cursor: default;
  }
  
  .num_jornada {
    color: #00ff6b;
    font-weight: bold;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.7);
    animation: pulseText 2s infinite alternate ease-in-out;
  }
  
  /* =============================
     ANUNCIOS
     ============================= */
  .ad-container {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    width: 100%;
    min-height: 150px;
    text-align: center;
    background: #ccc;
    font-family: 'Roboto', sans-serif;
    justify-content: center;
    
  }
  .main-layout .ad-container{
    max-width: 300px;
  }
  @media (max-width: 1000px) {
    .main-layout .ad-container{
      display: none;
    }
  }
  
  /* =============================
     LAYOUT PRINCIPAL
     ============================= */
  .main-layout {
    display: flex;
    flex: 1;
    justify-content: space-between;
    background: transparent;
    margin-bottom: 10px;
  }
  
  /* Contenido central */
  .central-content {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin: 20px;
    align-items: center;
    padding-top: 0px;
    background: transparent;
  }
  
  @media (max-width: 800px) {
    .central-content {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width: 600px) {
    .central-content {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  /* =============================
     CAJAS (Box) MODERNAS
     ============================= */
  .box {
    position: relative;
    height: 100px;
    background-color: #599a71;
    text-align: center;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    /* Borde blanco de 2px */
    border: 2px solid #ffffff;
    padding: 10px;
    font-family: 'Roboto', sans-serif;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .box:hover {
    transform: scale(1.05);
    /* Sombra turquesa suave */
    box-shadow: 0 25px 35px rgba(0, 255, 255, 0.2);
  }
  
  /* Triángulo decorativo en la esquina */
  .box::after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 45px;
    height: 45px;
    background: linear-gradient(
      135deg,
      transparent 10%,
      #177f5a 30%,
      #177f5a 45%,
      transparent 45%,
      transparent 60%,
      #177f5a 60%,
      #177f5a 75%,
      transparent 75%
    );
    clip-path: polygon(100% 0, 0 100%, 100% 100%);
    border-bottom-right-radius: 12px;
    opacity: 0.7; /* Más tenue */
  }
  
  /* Logos dentro de las cajas */
  .logo-vs-logo {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    overflow: hidden;
    position: relative;
  }
  
  .team-logo {
    width: 85px;
    height: 85px;
  }
  
  .vs-logo {
    width: 36px;
    height: 36px;
  }
  .loading-container {
  text-align: center;
  padding: 20px;
}



@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

  /* =============================
     RESPONSIVE MEDIA QUERIES
     ============================= */
  @media (max-width: 950px) {.predictions-title{ font-size: 1.5rem !important;}}
  @media (max-width: 800px) {
    .team-logo {
      width: 80px;
      height: 80px;
    }
    .clickable-boxes img {
      width: 60px;
      height: 60px;
    }
  
    .vs-logo {
      width: 33px;
      height: 33px;
    }
    .clickable-square {
      border-right: 1px solid #177f5a;
    }
    .featured-title {
      font-size: 1rem !important;
    }
    .predictions-title{ font-size: 1rem !important;}
  }
  
  @media (max-width: 520px) {
    .featured-title {
      font-size: 1rem !important;
    }
    .predictions-title{ font-size: 0.8rem !important;}
  }
  
  @media (max-width: 480px) {
    .team-logo {
      width: 70px;
      height: 70px;
    }
    .clickable-boxes img {
      width: 50px;
      height: 50px;
    }
    .vs-logo {
      width: 30px;
      height: 30px;
    }
  }
  
  @media (max-width: 440px) {
    .team-logo {
      width: 65px;
      height: 65px;
    }
    .clickable-boxes img {
      width: 45px;
      height: 45px;
    }
    .vs-logo {
      width: 27px;
      height: 27px;
    }
    .predictions-title {
      font-size: clamp(0.9rem, 2vw, 2rem);
      padding: 5px;
    }
  }
  
  @media (max-width: 410px) {
    .team-logo {
      width: 60px;
      height: 60px;
    }
    .vs-logo {
      width: 25px;
      height: 25px;
    }
    .predictions-title {
      font-size: clamp(0.7rem, 1.5vw, 1rem);
      padding: 5px;
    }
  }
  
  @media (max-width: 386px) {
    .team-logo {
      width: 56px;
      height: 56px;
    }
    .vs-logo {
      width: 24px;
      height: 24px;
    }
  }
  
  /* Posiciona la última caja en columna central (solo en pantallas grandes) */
  @media (min-width: 800px) {
    .central-content .box:last-child {
      grid-column: 2 / 3;
      justify-self: center;
      align-self: center;
      width: 100%;
    }
    .central-content {
      position: relative;
    }
  }
  
  
  /* =============================
     ANIMACIONES
     ============================= */
  
  /* Aparición */
  @keyframes fadeIn {
    from { opacity: 0; }
    to   { opacity: 1; }
  }
  
  /* Latido */
  @keyframes pulse {
    0%, 100% { transform: scale(1); }
    50%      { transform: scale(1.1); }
  }
  
  /* Brillo de color */
  @keyframes colorGlow {
    0%, 100% {
      border-color: #ffffff;
      box-shadow: 0 0 8px #ffffff, inset 0 0 5px rgba(0, 167, 61, 0.2);
    }
    50% {
      border-color: #00ff6b;
      box-shadow: 0 0 20px #00ff6b, inset 0 0 8px rgba(0, 167, 61, 0.4);
    }
  }
  
  /* Cambio de color en el número de jornada */
  @keyframes pulseText {
    0%   { color: #ffffff; }
    100% { color: #00ff6b; }
  }
  .loading-container {
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }
  
  .loading-spinner {
    width: 50px;
    height: 50px;
    border: 4px solid rgba(255, 255, 255, 0.3);
    border-top: 4px solid #ffffff;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    display: inline-block;
    margin-bottom: 10px;
  }
  
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

