/* Fondo general */
body, html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    font-family: 9, sans-serif;
    background: linear-gradient(135deg, #004d60, #002f3d); /* Fondo degradado */
}

/* Página de inicio de sesión */
.login-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh; /* Ocupar toda la altura de la ventana */
}

/* Contenedor del contenido principal */
.login-container {
    margin-top: 90px;
    flex: 1; /* Ocupa todo el espacio disponible */
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px; /* Evita que el contenido quede pegado a los bordes */
    
}

/* Contenedor general del formulario */
.login-form-wrapper {
    background: #ffffff; /* Fondo blanco */
    border-radius: 15px; /* Bordes redondeados */
    padding: 40px;
    width: 100%;
    max-width: 400px; /* Tamaño máximo */
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.3); /* Sombra */
    text-align: center; /* Centrar el contenido */
    border: 2px solid #00cc6f;
}

/* Título */
.login-form-wrapper h1 {
    font-size: 1.8rem;
    color: #004d60; /* Verde oscuro */
    margin-bottom: 20px;
    font-family: 'Roboto', serif;
    text-transform: uppercase;
    letter-spacing: 1.5px;
}

/* Inputs */
.login-form-wrapper input {
    width: 100%;
    padding: 12px;
    border-radius: 8px;
    border: 1px solid #ccc;
    font-size: 1rem;
    margin-bottom: 15px;
    background: #f7f7f7;
    outline: none;
    transition: border-color 0.3s ease, background-color 0.3s ease;
    color: black;
}

.login-form-wrapper input::placeholder {
    color: #888;
}

.login-form-wrapper input:focus {
    border-color: #00FF85; /* Verde al enfocarse */
    background-color: #eefaf1;
}

/* Botón */
.login-form-wrapper button {
    width: 100%;
    padding: 12px;
    border-radius: 8px;
    border: none;
    background-color: #00cc6f;
    color: #ffffff;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
    margin-top: 10px;
    transition: background-color 0.3s ease, transform 0.2s ease;
    border: 2px solid #002f3d;
}

.login-form-wrapper button:hover {
    background-color: #00cc6f;
    transform: scale(1.02);
}

/* Error y Éxito */
.error {
    color: #ff4d4d;
    font-size: 0.9rem;
    margin-top: 10px;
}

.success {
    color: #00cc6f;
    font-size: 0.9rem;
    margin-top: 10px;
}

/* Registro */
.login-form-wrapper p {
    color: #004d60;
    margin-top: 20px;
    font-size: 0.9rem;
}

.login-form-wrapper p a {
    color: #00FF85;
    font-weight: bold;
    text-decoration: none;
}

.login-form-wrapper p a:hover {
    text-decoration: underline;
}

/* Footer siempre al final */
footer {
    background: #002f3d;
    color: #ffffff;
    text-align: center;
    padding: 10px;
    font-size: 0.9rem;
    margin-top: auto; /* Empuja el footer hacia abajo */
}

/* Ajustes responsivos */
@media (max-width: 768px) {
    .login-form-wrapper {
        padding: 30px;
        max-width: 320px;
    }

    .login-form-wrapper h1 {
        font-size: 1.5rem;
    }
}

@media (max-width: 480px) {
    .login-form-wrapper {
        padding: 20px;
        max-width: 280px;
    }

    .login-form-wrapper h1 {
        font-size: 1.2rem;
    }
}
