@font-face {
    font-family: 'Roboto';
    font-weight: 400;
    src: url('../../public/fonts/roboto-regular.ttf') format('truetype');
}

@font-face {
font-family: 'Roboto-bold';
font-weight: 700;
src: url('../../public/fonts/roboto-bold.ttf') format('truetype');
}

@font-face {
font-family: 'Merriweather';
font-weight: 700;
src: url('../../public/fonts/merriweather-bold.ttf') format('truetype');
}

@font-face {
font-family: 'Jersey M54';
src: url('../../public/fonts/jersey-m54.ttf') format('truetype');
}

* {
box-sizing: border-box;
}

body, html {
overflow-x: hidden;
margin: 0;
padding: 0;
width: 100%;
font-family: 'Roboto-bold', sans-serif;
background: linear-gradient(90deg, #000, #80deea, #000) ;
color: #333;
}

.box-page-layout {
display: flex;
flex-direction: column;
align-items: center;
min-height: 100vh;
}

.box-page-content {
display: flex;
flex-direction: column;
align-items: center;
width: 100%;
max-width: 1000px;
gap: 20px;
padding-top: 20px;
}

.box-item {
width: 100%;
max-width: 900px;
padding: clamp(15px, 2vw, 25px);
border-radius: 20px;
background-color: #ffffff;
box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
text-align: center;
transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.team-header {
display: flex;
align-items: center;
justify-content: center;
gap: 10px;
padding: 10px 20px;
margin-bottom: 35px;
border-radius: 12px;
background: linear-gradient(135deg, #0A1F3A, #2E4A67);
box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
border: 1px solid rgba(255, 255, 255, 0.3);
}

.team-header h2 {
color: #E0F7FA;
font-size: clamp(1.5rem, 4vw, 2.4rem);
font-weight: 700;
margin: 0;
text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3);
font-family: 'Roboto', sans-serif;
}

.team-logo-title {
width: clamp(60px, 20vw, 90px);
height: auto;
border-radius: 50%;
box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
background-color: rgba(255, 255, 255, 0.1);
padding: 5px;
}
.orden-select {
font-family: 'Roboto', sans-serif;
right: 20px; /* Alinea hacia la derecha */
top: 50%; /* Centra verticalmente */
transform: translateY(-50%); /* Ajusta el centro vertical */
padding: 6px 10px; /* Ajuste del tamaño del input */
font-size: 0.9rem; /* Más pequeño para pantallas pequeñas */
border-radius: 6px;
background: linear-gradient(135deg, #2E4A67, #0A1F3A);
color: #E0F7FA;
box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
transition: all 0.3s ease;
}

.orden-select:hover {
background: linear-gradient(135deg, #3D5A78, #102C4A);
cursor: pointer;
}
.orden-select option {
background-color: #0A1F3A; /* Fondo oscuro para las opciones */
color: #2196F3; /* Texto azul en las opciones desplegadas */
padding: 5px 10px; /* Espaciado interno de las opciones */
}


.jugadores-table {
width: 100%;
border-collapse: separate;
border-spacing: 0;
background: linear-gradient(145deg, #f7f9fc, #e4e9f2);
border-radius: 15px;
overflow: hidden;
box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.15), 
            inset 0px -3px 8px rgba(255, 255, 255, 0.4);
}

.jugadores-table th,
.jugadores-table td {
padding: clamp(8px, 1.5vw, 12px);
font-size: clamp(0.8rem, 1.8vw, 1.2rem);
text-align: center;
vertical-align: middle;
background-color: #f0f4f8;
box-shadow: inset 0px -1px 2px rgba(255, 255, 255, 0.5), inset 0px 1px 2px rgba(0, 0, 0, 0.1);
}

.jugadores-table th {
background: linear-gradient(135deg, #0A1F3A, #1F3759);
color: #ffffff;
text-transform: uppercase;
font-weight: 700;
padding: 14px;
}

.jugadores-table .dorsal {
font-family: 'Jersey M54', sans-serif;
font-size: clamp(1rem, 2vw, 1.4rem);
color: #333;
}

.jugadores-table td:first-child {
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
font-weight: bold;
font-size: 1.2rem;
padding: 12px;
}

.player-image {
width: clamp(50px, 10vw, 80px);
height: clamp(50px, 10vw, 80px);
border-radius: 50%;
object-fit: cover;
box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
margin-bottom: 5px;
background: radial-gradient(circle, rgba(0, 206, 209, 0.8), #003366, rgba(0, 0, 0, 0.9));
}

/* Colores para las probabilidades con efecto botón */
.jugadores-table .rojo,
.jugadores-table .naranja,
.jugadores-table .verde,
.jugadores-table .azul {
font-weight: bold;
color: #fff;
border-radius: 20px;
padding: 10px 15px;
text-align: center;
display: inline-block;
min-width: 60px;
transition: transform 0.2s ease, box-shadow 0.2s ease;
box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.jugadores-table .rojo {
background: linear-gradient(145deg, #ff4d4d, #ff7979);
}

.jugadores-table .naranja {
background: linear-gradient(145deg, #ffae42, #ffc94f);
}

.jugadores-table .verde {
background: linear-gradient(145deg, #28a745, #4ecf68);
}

.jugadores-table .azul {
background: linear-gradient(145deg, #339EB2, #5ab9cc);
}

.jugadores-table .rojo:hover,
.jugadores-table .naranja:hover,
.jugadores-table .verde:hover,
.jugadores-table .azul:hover {
transform: translateY(-2px);
box-shadow: 0 6px 12px rgba(0, 0, 0, 0.25);
}

/* Iconos de alineación */
.tick-icon, .cross-icon {
width: clamp(25px, 5.5vw, 40px);
height: auto;
cursor: pointer;
transition: transform 0.2s ease, box-shadow 0.2s ease;
}

/* Disclaimer */
.rubrica {
width: 90%;
max-width: 800px;
background-color: transparent;
color: #e4e9f2;
padding: 10px;
text-align: center;
margin-top: 85px;
border-radius: 8px;
cursor: pointer;
font-weight: bold;
font-size: 1rem;
position: relative;
z-index: 10;
}
.rubrica-content{
background-color: transparent !important;
font-size: 1rem;
}
.rubrica.open {
background-color: #000;
}

/* Icono de flecha */
.arrow-icon {
width: 20px;
height: 20px;
transition: transform 0.3s ease;
}

.rubrica.open .arrow-icon {
transform: rotate(180deg);
}

/* Ajustes responsivos para pantallas medianas (máximo 750px) */
@media (max-width: 750px) {
.jugadores-table th, 
.jugadores-table td {
    font-size: 0.9rem;
    padding: 8px;
}

/* Tamaño incrementado de la imagen del jugador */
.player-image {
    width: 70px; /* Incrementado a 70px */
    height: 70px;
}
}

/* Ajustes para pantallas pequeñas (máximo 500px) */
@media (max-width: 600px) {
.box-page-content {
    padding-top: 15px;
}

.box-item {
    padding: clamp(10px, 2vw, 15px);
}

.team-header {
    gap: 20px;
}
.team-header h2 {
    font-size: clamp(1.5rem, 3.5vw, 1.9rem);
}

.jugadores-table th, 
.jugadores-table td {
    font-size: 0.75rem;
    padding: 4px;
}

/* Tamaño incrementado de la imagen del jugador */
.player-image {
    width: 65px; /* Incrementado a 65px */
    height: 65px;
}

.jugadores-table .dorsal {
    font-size: 1rem;
}

.tick-icon, 
.cross-icon {
    width: 25px;
    height: auto;
}

.jugadores-table .rojo, 
.jugadores-table .naranja, 
.jugadores-table .verde, 
.jugadores-table .azul {
    font-size: 0.75rem;
    padding: 4px;
    border-radius: 10px;
}

.jugadores-table td:first-child {
    font-size: 0.85rem;
    padding: 6px;
}
}



@media (max-width: 510px) {
.box-page-content {
    padding-top: 10px;
}

.box-item {
    padding: 8px;
    border-radius: 15px;
}

.team-header h2 {
    font-size: clamp(1rem, 3vw, 1.4rem);
    padding: 5px;
}

.team-logo-title {
    width: 50px;
    height: 50px;
}

.jugadores-table th, 
.jugadores-table td {
    font-size: 0.65rem;
    padding: 5px;
}

/* Tamaño de la imagen del jugador ajustado */
.player-image {
    width: 55px; /* Incrementado a 55px para pantallas pequeñas */
    height: 55px;
}

.jugadores-table .dorsal {
    font-size: 0.85rem;
}

.tick-icon, 
.cross-icon {
    width: 18px;
    height: auto;
}

/* Ajustes de tamaño y padding para las celdas de colores */
.jugadores-table .rojo, 
.jugadores-table .naranja, 
.jugadores-table .verde, 
.jugadores-table .azul {
    font-size: 0.6rem;
    padding: 3px 6px;
    border-radius: 6px;
    min-width: 40px;
}

.jugadores-table td:first-child {
    font-size: 0.75rem;
    padding: 4px;
}
}

/* Ajustes para pantallas ultra pequeñas (menor a 375px) */
@media (max-width: 395px) {
.team-header h2 {
    font-size: clamp(1rem, 3vw, 1.4rem);
}
}
@media (max-width: 374px) {
.box-item {
    padding: 5px;
}


.jugadores-table th, 
.jugadores-table td {
    font-size: 0.6rem;
    padding: 3px;
}

/* Tamaño de la imagen del jugador ajustado */
.player-image {
    width: 50px; /* Incrementado a 50px para pantallas ultra pequeñas */
    height: 50px;
}

.jugadores-table .dorsal {
    font-size: 0.8rem;
}

.tick-icon, 
.cross-icon {
    width: 16px;
    height: auto;
}

.jugadores-table .rojo, 
.jugadores-table .naranja, 
.jugadores-table .verde, 
.jugadores-table .azul {
    font-size: 0.5rem;
    padding: 2px 4px;
    border-radius: 4px;
    min-width: 35px;
}

.jugadores-table td:first-child {
    font-size: 0.7rem;
    padding: 3px;
}
}

.loading-container {
position: absolute;
top: 70%;
left: 50%;
transform: translate(-50%, -50%);
text-align: center;
}

.loading-spinner {
width: 50px;
height: 50px;
border: 4px solid rgba(255, 255, 255, 0.3);
border-top: 4px solid #ffffff;
border-radius: 50%;
animation: spin 1s linear infinite;
display: inline-block;
margin-bottom: 10px;
}


@keyframes spin {
0% { transform: rotate(0deg); }
100% { transform: rotate(360deg); }
}
