body{
    background: linear-gradient(to bottom right, #004d60, #001f29);
}
.privacy-policy {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    line-height: 1.6;
    margin-top: 80px;
    color: #333;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.privacy-policy h1 {
    font-size: 2em;
    margin-bottom: 20px;
    color: #003366;
    text-align: center;
}

.privacy-policy h2 {
    font-size: 1.5em;
    color: #005580;
    margin-top: 20px;
}

.privacy-policy p {
    margin-bottom: 15px;
}

.privacy-policy ul {
    list-style-type: disc;
    padding-left: 20px;
}

.privacy-policy a {
    color: #339EB2;
    text-decoration: none;
    font-weight: bold;
}

.privacy-policy a:hover {
    text-decoration: underline;
}
