.news-container {
  display: grid;
  grid-template-columns: repeat(6, 1fr); /* Siempre 6 columnas en pantallas grandes */
  gap: 12px;
  margin: 20px 0;
  padding: 10px;
}

/* Para pantallas medianas (tabletas) */
@media (max-width: 1024px) {
  .news-container {
    grid-template-columns: repeat(3, 1fr); /* Cambia a 3 columnas */
  }
}

/* Para pantallas pequeñas (móviles) */
@media (max-width: 600px) {
  .news-container {
    grid-template-columns: repeat(2, 1fr); /* Cambia a 2 columnas para mejor visualización en móviles */
  }
}

/* Opcional: para móviles muy pequeños */
@media (max-width: 400px) {
  .news-container {
    grid-template-columns: 1fr; /* Una columna en pantallas muy pequeñas */
  }
}
.news-content {
  /* Empuja el contenido hacia abajo */
  margin-top: auto;
}
.news-card {
  background-color: #FFFFFFE6;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(4px);
  font-family: 'Roboto', sans-serif;
  
  /* Se agrega flexbox para distribuir los elementos verticalmente */
  display: flex;
  flex-direction: column;
}

.news-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}


.news-image-container {
  position: relative;
  width: 100%;
  height: 200px;      /* Altura fija */
  overflow: hidden;   /* Para recortar la parte que sobrepase */
}

.news-image {
  width: 100%;
  height: 100%;
  object-fit: cover;  
  object-position: bottom; /* Muestra la parte de abajo y recorta lo de arriba */
  display: block;
}



.news-card:hover .news-image {
  filter: brightness(1.05);
}

.news-date {
  position: absolute;
  bottom: 10px;
  left: 10px;
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  padding: 4px 10px;
  border-radius: 5px;
  font-size: 0.85rem;
  font-weight: 500;
  font-family: 'Roboto', sans-serif;
}

.news-bar {
  padding: 10px 15px;
  font-size: 1.1rem;
  font-family: 'Archivo Black', sans-serif;
  color: #333;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.95);
  border-top: 1px solid rgba(0, 0, 0, 0.05);
  transition: color 0.3s ease;
  margin-top: 25px;
}

.news-title {
  padding: 10px 15px;
  font-size: 1.05rem;
  font-family: 'Roboto', sans-serif;
  color: #333;
  text-align: center;
  transition: color 0.3s ease;
  justify-content: center;
  align-items: center;
  
}

.news-title:hover {
  color: #005a8d;
}
