/* ------------------------------
   Contenedor general de la página
------------------------------ */
.noticias-recap-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background: #f9f9f9;
    color: #333;
    font-family: 'Roboto', sans-serif;
    padding-top: 90px;
  }
  
  /* ------------------------------
     Estructura de tres columnas con espacio y centrado
  ------------------------------ */
  .noticias-recap-layout {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 20px;
    box-sizing: border-box;
    gap: 20px;
    
  }
  
  /* ------------------------------
     Columnas laterales para anuncios
  ------------------------------ */
  .ad-recap-lateral {
    width: 160px;
    height: 600px; /* Altura estándar para anuncios laterales */
    display: none; /* Oculto por defecto, visible en pantallas grandes */
    position: sticky;
    top: 50%; /* Centrado vertical */
    transform: translateY(-50%);
    background: #f1f1f1;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    padding: 0;
    overflow: hidden;
    box-sizing: border-box;
    
  }
  
  /* Contenedor interno del anuncio */
  .ad-container-recap {
    width: 100%;
    height: 100%;
    background-color: #cccccc; /* Simulación de espacio para anuncio */
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 90px;
  }
  
  .ad-container-recap iframe {
    width: 100%;
    height: 100%;
    border: none;
    display: block;
  }
  
  /* ------------------------------
     Columna central (contenedor de las noticias)
  ------------------------------ */
  .news-container-recap {
    flex: 1;
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 6px 14px rgba(0, 0, 0, 0.1);
    padding: 25px 30px;
    max-width: 900px;
    width: 100%;
    animation: fadeIn 0.8s ease-in-out;
    min-height: 900px;
  }
  
  /* ------------------------------
     Título principal
  ------------------------------ */
  .noticias-recap-title {
    font-family: 'Archivo Black', sans-serif;
    font-size: 1.8rem; /* Tamaño un poco mayor para destacar */
    color: #ffffff; /* Texto blanco */
    text-align: center;
    margin: 0;
    padding: 15px 40px; /* Más padding para hacerlo más prominente */
    text-transform: uppercase;
    border-radius: 12px; /* Bordes más redondeados */
    /* Borde blanco con sombra */
    border: 2px solid #ffffff;
    background: linear-gradient(135deg, #004d60, #007a2e); /* Gradiente sutil */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7); /* Sombra para que el texto resalte */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Sombra del contenedor */
    transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
    cursor: pointer; /* Indicador de clic */
    margin-bottom: 20px;
  }
  
  /* ------------------------------
     Estilo de las noticias (tipo lista)
  ------------------------------ */
  .news-image-container {
    position: relative;
    width: 100%;
    height: 200px; /* Ajusta la altura según tu preferencia */
    overflow: hidden; /* Recorta la parte sobrante de la imagen */
  }
  
  .news-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top; /* Muestra la parte superior y recorta el resto */
  }
  .news-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #f9f9f9;
    border-radius: 8px;
    padding: 12px 16px;
    text-decoration: none;
    color: #333;
    margin-bottom: 10px;
    transition: background-color 0.2s ease, transform 0.2s ease;
    border-left: 4px solid transparent;
    
  }
  
  .news-item:hover {
    background-color: #f0f0f0;
    border-left-color: #005a8d;
    transform: translateY(-3px);
  }
  
  .news-item-content {
    display: flex;
    align-items: center;
    flex: 1;
    gap: 10px;
  }
  
  .news-title {
    font-size: 1.1rem;
    font-weight: 500;
    color: #222;
    flex: 1;
    text-align: left;
  }
  
  .news-date-recap {
    font-size: 0.9rem;
    color: #777;
    white-space: nowrap;
  }
  
  /* ------------------------------
     Cargando noticias
  ------------------------------ */
  .loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: #f9f9f9;
  }
  
  .loading-text {
    font-size: 1.2rem;
    color: #666;
  }

  .pagination-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px 0;
    gap: 10px;
    margin-top: auto; /* ✅ Esta línea empuja el botón hacia la parte inferior del contenedor */
    margin-bottom: 0;
    
  }
  
  .pagination-button {
    background-color: #001f29;
    color: white;
    padding: 8px 16px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s ease;
    
  }
  
  .pagination-button:disabled {
    background-color: #cccccc;
    cursor: not-allowed;
  }
  
  .pagination-info {
    font-size: 14px;
  }
  
  
  /* ------------------------------
     Responsividad
  ------------------------------ */
  
  /* Mostrar anuncios laterales solo en pantallas grandes */
  @media (min-width: 1024px) {
    .ad-recap-lateral {
      display: block;
    }
  }
  
  /* Ajustes para tabletas */
  @media (max-width: 768px) {
    .noticias-recap-title {
      font-size: 2rem;
    }
  
    .news-title {
      font-size: 1rem;
    }
  
    .news-date-recap {
      font-size: 0.85rem;
    }
  
    .ad-recap-lateral {
      display: none; /* Ocultar anuncios en tabletas */
    }
  }
  
  /* Ajustes para móviles */
  @media (max-width: 600px) {
    .news-item {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .news-item-content {
      flex-direction: column;
      gap: 6px;
    }
  
    .news-date-recap {
      align-self: flex-end;
    }
  }
  
  /* ------------------------------
     Animación de entrada
  ------------------------------ */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-8px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  