/* ========================= */
/* 🏆 Squad Page - Estilos 🏆 */
/* ========================= */

/* Fuentes y Reset */
@font-face {
  font-family: 'Roboto';
  font-weight: 400;
  src: url('../../public/fonts/roboto-regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Roboto-bold';
  font-weight: 700;
  src: url('../../public/fonts/roboto-bold.ttf') format('truetype');
}

@font-face {
  font-family: 'Merriweather';
  font-weight: 700;
  src: url('../../public/fonts/merriweather-bold.ttf') format('truetype');
}

@font-face {
  font-family: 'Jersey M54';
  src: url('../../public/fonts/jersey-m54.ttf') format('truetype');
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body, html {
  font-family: 'Roboto-bold', sans-serif;
  color: #333;
  height: 100%;
}

/* ========================= */
/* Contenedor principal de la Squad Page */
.squad-page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Contenedor central */
.squad-main-square {

  margin-bottom: 5px;
  display: flex;
  justify-content: center;
  padding: 20px;

}

/* Se ajusta el ancho en pantallas grandes al 80% */
.squad-page {
  display: flex;
  flex-direction: row;
  gap: 30px;
  padding: 20px;
  width: 90%;
  max-width: 1400px;
  margin: 0 auto;
  background: #fff;
  border-radius: 12px;
}

@media (min-width: 1200px) {
  .squad-page {
    width: 80vw;
  }
}

/* ========================= */
/* Sección de Alineación     */
/* ========================= */

.squad-alineacion-result {
  flex: 1;
  padding: 20px;
  text-align: center;
}

.squad-alineacion-result h2 {
  font-family: 'Archivo Black', sans-serif;
  font-size: 1.8rem; /* Tamaño un poco mayor para destacar */
  color: #ffffff; /* Texto blanco */
  text-align: center;
  margin: 0;
  padding: 15px 40px; /* Más padding para hacerlo más prominente */
  text-transform: uppercase;
  border-radius: 12px; /* Bordes más redondeados */
  /* Borde blanco con sombra */
  border: 2px solid #ffffff;
  background: linear-gradient(135deg, #004d60, #007a2e); /* Gradiente sutil */
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7); /* Sombra para que el texto resalte */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Sombra del contenedor */
  transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
  cursor: pointer; /* Indicador de clic */
  margin-bottom: 20px;
}

/* Contenedor de la formación (simulación de campo de fútbol) */
.squad-formation-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  background: linear-gradient(to bottom, #2e7d32, #388e3c);
  border-radius: 12px;
  padding: 20px;
  color: #fff;
  min-height: 75vh; /* Ajuste de altura para cubrir más pantalla */
  width: 95vw; /* Hacerlo más ancho */
  max-width: 1000px; /* Máximo tamaño en pantallas grandes */
  border: 2px solid #f0f0f0;
  position: relative;
}

/* Área del centro del campo */
.squad-formation-display::before {
  content: '';
  position: absolute;
  width: 100px;
  height: 100px;
  border: 2px solid rgba(255,255,255,0.8);
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Línea central del campo */
.squad-formation-display::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: rgba(255,255,255,0.8);
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

/* Cada fila (categoría) en la formación */
.squad-line {
  display: flex;
  flex-wrap: wrap;        /* Permite envolver en móviles */
  justify-content: center; 
  align-items: flex-start; /* Ancla las tarjetas desde arriba */
  gap: 20px;
  width: 100%;
  margin: 10px 0;
  z-index: 100;
}

/* Tarjeta de cada jugador en la formación */
.squad-player-box-squad {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; 
  width: 100px;       /* Ancho fijo */
  height: 150px;      /* Alto fijo para alinear la imagen */
  border-radius: 10px;
  padding: 10px;
  overflow: hidden;    
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Imagen del jugador anclada arriba */
.squad-player-img {
  width: 100px;
  height: 100px;
  border-radius: 10%;
  margin-bottom: 8px; 
  border: 2px solid #fff;
 
  background: radial-gradient(circle, rgba(0,206,209,0.8), #003366, rgba(0,0,0,0.9));
}

/* Nombre debajo de la imagen, sin recorte para mostrar completo */
.squad-player-name-squad {
  font-size: 0.9rem;
  font-family: 'Roboto', sans-serif;
  color: #fafafa;
  text-align: center;
  margin: 0;
  white-space: normal;       /* Permite varias líneas si es largo */
  word-wrap: break-word;
  overflow-wrap: break-word;
}

/* Caso sin portero u otro slot */
.squad-empty-slot {
  font-size: 1rem;
  font-weight: bold;
  color: #ddd;
  text-align: center;
}

/* Botón "Volver" */
.squad-back-btn {
  background: #177f5a;   /* Verde */
  color: #fff;
  padding: 12px 20px;
  font-size: 1.2rem;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s, transform 0.2s;
  margin-top: 20px;
}

.squad-back-btn:hover {
  transform: scale(1.05);
}

/* ========================= */
/* Sección de Jugadores (Selección) */
/* ========================= */
.squad-selection-section {
  flex: 1.2;
  background-color: #F8F8FF;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

.squad-category-section {
  margin-bottom: 30px;
}

.squad-category-section h3 {
  margin-bottom: 15px;
  font-size: 3.8rem;
  text-align: center;
  font-family: 'Jersey M54', sans-serif;
  color: #177f5a;
  padding-bottom: 10px;
}

/* Grilla de selección por categoría */
.squad-category-grid {
  display: grid;
  gap: 15px;
  align-items:self-end;
  padding: 10px;
  grid-template-columns: repeat(2, 180px); /* Por defecto, 2 columnas */
}

@media (min-width: 768px) {
  .squad-category-grid {
    grid-template-columns: repeat(3, 180px);
  }
}

@media (min-width: 1024px) {
  .squad-category-grid {
    grid-template-columns: repeat(4, 180px);
  }
}

/* Tarjeta de Jugador (Selección) */
.squad-player-slot {
  width: 180px;    
  height: 240px;   
  background: #fafafa;
  border: 1px solid #003366;
  border-radius: 8px;
  text-align: center;
  transition: transform 0.2s ease-in-out, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.squad-player-slot:hover {
  transform: scale(1.03);
  box-shadow: 0 6px 12px rgba(0,0,0,0.1);
}

/* Inputs de Jugadores */
.squad-input-container {
  position: relative;
  margin-bottom: 15px;
  margin: 10px;
}

.squad-input-container input {
  width: 100%;
  height: 40px;
  padding: 10px 35px 10px 10px;
  border: 2px solid #2E4A67;
  border-radius: 8px;
  font-size: 1rem;
  background-color: #fff;
  color: #000;
}

.squad-input-container input::placeholder {
  color: #666;
}

.squad-clear-btn {
  position: absolute;
  right: 5px;
  top: 5px;
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 1.2rem;
  color: #666;
}

/* Lista de sugerencias */
.squad-suggestions {
  list-style: none;
  padding: 0;
  border: 1px solid #ccc;
  background: #fff;
  position: absolute;
  width: 100%;
  z-index: 1000;
  margin-top: 5px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  max-height: 200px;
  overflow-y: auto;
}

.squad-suggestions li {
  padding: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.3s;
}

.squad-suggestions li:hover {
  background: #f0f0f0;
}

/* Imagen y Nombre de Jugadores en la Selección */
.squad-player-info {
  background: linear-gradient(135deg, #0A1F3A, #2E4A67);
  border-radius: 15px;
  padding: 10px;
  width: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-bottom: 4px solid #177f5a;
  border-top: 4px solid #177f5a;
}

.squad-player-info img {
  width: 120px;   
  height: 120px;  
  border-radius: 10%;
  object-fit: cover;
  box-shadow: 0 4px 8px rgba(0,0,0,0.15);
  margin-bottom: 5px;
  background: radial-gradient(circle, rgba(0,206,209,0.8), #003366, rgba(0,0,0,0.9));
  border: 2px solid #e4e9f2;
}

/* Texto con elipsis si es muy largo en la selección */
.squad-player-info span {
  display: block;
  width: 100%;
  font-weight: bold;
  font-size: 0.9rem;
  color: #e4e9f2;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Botones (Calcular, Guardar) */
.squad-calculate-btn,
.squad-save-btn {
  padding: 12px 20px;
  font-size: 1.2rem;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s, transform 0.2s;
  width: 100%;
  margin-left: 30px;
  margin-right: 30px;
}

.squad-selection-view{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.squad-selection-view h2{
  font-family: 'Archivo Black', sans-serif;
    font-size: 1.8rem; /* Tamaño un poco mayor para destacar */
    color: #ffffff; /* Texto blanco */
    text-align: center;
    margin: 0;
    padding: 15px 40px; /* Más padding para hacerlo más prominente */
    text-transform: uppercase;
    border-radius: 12px; /* Bordes más redondeados */
    /* Borde blanco con sombra */
    border: 2px solid #ffffff;
    background: linear-gradient(135deg, #004d60, #007a2e); /* Gradiente sutil */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7); /* Sombra para que el texto resalte */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Sombra del contenedor */
    transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
    cursor: pointer; /* Indicador de clic */
    margin-bottom: 20px;
}
.squad-calculate-btn {
  background: #177f5a;
  color: #fff;
}


.squad-save-btn {
  background: #4CAF50;
  color: #fff;
}

/* ========================= */
/* Loading Spinner           */
/* ========================= */
.loading-container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(0,0,0,0.6);
  width: 100vw;
  height: 100vh;
  z-index: 9999;
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid rgba(255,255,255,0.3);
  border-top: 4px solid #fff;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 10px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* ========================= */
/* Ajustes Responsivos       */
/* ========================= */
@media (max-width: 600px) {
  /* Reduce el tamaño de la tarjeta del jugador en la formación */
  .squad-player-box-squad {
    width: 80px;
    height: 120px;
  }
  
  .squad-player-img {
    width: 50px;
    height: 50px;
  }
  
  .squad-player-name-squad {
    font-size: 0.8rem;
  }
}
/* Contenedor del botón para centrarlo */
.squad-buttons-row-quad {
  display: flex;
  justify-content: center; /* Centra horizontalmente */
  align-items: center; /* Centra verticalmente */
  width: 100%; /* Asegura que el contenedor ocupe todo el ancho */
  margin-top: 20px; /* Espaciado superior */
}

/* Ajustar los botones para mantener proporción */
.squad-calculate-btn,
.squad-save-btn {
  padding: 12px 20px;
  font-size: 1.2rem;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s, transform 0.2s;
  width: auto; /* Ajusta el tamaño del botón automáticamente */
  min-width: 200px; /* Tamaño mínimo para que se vea bien */
  text-align: center;
}



.squad-calculate-btn:hover {

  transform: scale(1.05);
}


.squad-save-btn:hover {
  transform: scale(1.05);
}

