@font-face {
  font-family: 'Jersey M54';
  src: url('../../public/fonts/jersey-m54.ttf') format('truetype');
}

@font-face {
  font-family: 'Roboto';
  src: url('../../public/fonts/roboto-regular.ttf') format('truetype');
}

.position-layout {
  padding: 20px;
  margin-top: 90px; /* Ajuste para el header */
}

.position-category-box {
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 15px;
  border-radius: 10px;
  color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  background: rgba(0, 0, 0, 0.6); /* Fondo semitransparente */
  border: 2px solid #ffffff;
}

.position-category-box h3 {
  margin: 0;
  text-align: center;
  font-size: 2em;
  font-family: 'Jersey M54', sans-serif;
  color: #ffffff;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3);
  padding-bottom: 10px;
}

.position-player-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 4 jugadores por fila */
  gap: 25px;
  justify-content: center;
  padding: 20px;
}

.position-player-card {
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.position-player-image {
  width: 80px;
  height: 80px;
  border-radius: 10%;
  border: 3px solid #ffffff;
  transition: transform 0.2s ease;
  background: radial-gradient(circle, rgba(0, 206, 209, 0.8), #003366, rgba(0, 0, 0, 0.9));
}

.position-player-image:hover {
  transform: scale(1.1);
}

.position-player-card p {
  margin: 5px 0 0 0;
  font-size: 1em;
  font-family: 'Roboto', sans-serif;
  color: #ffffff;
}

/* Colores de categoría */
.rojo {
  background-color: #FF5733;
}

.naranja {
  background-color: #FFC300;
}

.verde {
  background-color: #4CAF50;
}

.azul {
  background-color: #03A9F4;
}

/* Ajustes responsivos para pantallas medianas (máximo 750px) */
@media (max-width: 750px) {
  .position-player-image {
    width: 70px;
    height: 70px;
  }

  .position-player-card p {
    font-size: 0.9em;
  }

  .position-category-box h3 {
    font-size: 1.8em;
  }
}

/* Ajustes para pantallas pequeñas (máximo 500px) */
@media (max-width: 500px) {
  .position-player-image {
    width: 60px;
    height: 60px;
  }

  .position-player-card p {
    font-size: 0.8em;
  }

  .position-category-box h3 {
    font-size: 1.5em;
  }
}

/* Ajustes para pantallas muy pequeñas (máximo 375px) */
@media (max-width: 375px) {
  .position-player-image {
    width: 50px;
    height: 50px;
  }

  .position-player-card p {
    font-size: 0.7em;
  }

  .position-category-box h3 {
    font-size: 1.2em;
  }
}
.position-layout h1 {
  font-family: 'Archivo Black', sans-serif;
    font-size: 1.8rem; /* Tamaño un poco mayor para destacar */
    color: #ffffff; /* Texto blanco */
    text-align: center;
    margin: 0;
    padding: 15px 40px; /* Más padding para hacerlo más prominente */
    text-transform: uppercase;
    border-radius: 12px; /* Bordes más redondeados */
    /* Borde blanco con sombra */
    border: 2px solid #ffffff;
    background: linear-gradient(135deg, #004d60, #007a2e); /* Gradiente sutil */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7); /* Sombra para que el texto resalte */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3); /* Sombra del contenedor */
    transition: transform 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
    cursor: pointer; /* Indicador de clic */
    margin-bottom: 10px;
    
}

